body {
    font-family: Manrope, sans-serif;
    background-color:  #FDFAEC;
}
.main-container {
    padding-top: 80px;
    padding-left: 150px;
    font-family: 'Manrope', sans-serif;
}
.heading {
    font-size: 36px;
    color: #333;
    margin-bottom: 20px;
}
.form-container {
    background-color: #fff;
    padding: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
}

.recipe-container {
    display: flex;
    flex-wrap: wrap;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.recipe-item-container {
    /*flex: 1 1 25%;*/
    text-align: center;
    margin: 10px;
    white-space: nowrap; /* Prevent text from wrapping */
    text-overflow: ellipsis; /* Add an ellipsis (...) for overflow */
    border: 1px solid #ccc; /* Add a border to better visualize the container */
    padding: 1rem;
    background-color: #E4E4D0;
    border-radius: 0.25rem;
}

.welcome-user {
    font-weight: 600;
    color: #2E8A99;
    
}

.input-text::placeholder {
  color: #CACACA;
}

h3 {
    display: flex;
    padding-left: 14.75rem;
    padding-top: 5rem;
    color: #2E8A99

}

.btn-div {
    display: inline;
    position: relative;
    left: 0.75rem;
}
.btn-primary {
    background-color: #2E8A99;
    border-color:#2E8A99;
}


.input-form {
    display: flex;
    padding-left: 15rem;
    padding-top: 1rem;
}
form {
    width: 20rem;
}